@charset "utf-8";
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");
/* 가변글꼴: @import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/variable/pretendardvariable.css'); */
html, body, iframe { height: 100%; margin: 0 0; padding: 0 0; }
body { width: 100%; font-family: Pretendard; } 

/* 공통 버튼 */
.ks-btn { @apply box-border flex justify-center items-center pt-1.5 pr-2.5 pb-1.5 pl-2.5 min-w-14 leading-5 text-sm outline-none bg-white text-black border border-gray-300 no-underline rounded; }
.ks-btn:hover { filter: brightness(.9); }
.ks-btn.primary { @apply bg-[#0f6cbd] border-0 text-white; }
.ks-btn.primary:hover { filter: brightness(.9); }
.ks-btn + .ks-btn { @apply ml-[6px]; }
.ks-btn:disabled { @apply bg-[#dddddd] text-white border-[#dddddd] }
.ks-btn:disabled:hover { filter: none; }
.ks-btn.ks-btn-ico {
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px;
}

.ks-ibtn {
    @apply box-border p-0 outline-none bg-white bg-no-repeat bg-center w-4 h-4 text-black border-none border-gray-300 no-underline rounded indent-[-9999px] overflow-hidden whitespace-nowrap cursor-pointer;
    background-size: 16px 16px;

}
.ks-ibtn:hover { filter: brightness(.9); }
.ks-ibtn:disabled { background-color: #dddddd; color: white; }
.ks-ibtn:disabled:hover { filter: none; }
.ks-ibtn + .ks-ibtn { margin-left: 6px; }

.ks-ico-list-mng { background-image: url('./assets/images/ico-list-mng.svg'); }
.ks-ico-save { background-image: url('./assets/images/ico-save.svg'); }
.ks-ico-login { background-image: url('./assets/images/ico-login.svg'); }
.ks-ico-logout { background-image: url('./assets/images/ico-logout.svg'); }
.ks-ico-close { background-image: url('./assets/images/ico-cross.svg'); }
.ks-ico-morev { background-image: url('./assets/images/ico-more-vertical.svg'); }

/* --------------------- 폼 박스 ------------------------- */
.ks-formbox {
    border-radius: 4px;
    border: 1px solid #d1d1d1;
    padding: 16px 12px 12px 12px;
    position: relative;
    box-sizing: border-box;
    min-height: 48px;
}
.ks-formbox > label {
    position: absolute;
    top: -10px;
    background-color: white;
    font-size: 14px;
    color: #a1a1a1;
    padding: 0 7px;
}
.ks-formbox:focus-within > label {
    font-weight: bold;
    color: #000;
}
.ks-formbox input[type="text"] {
    font-size: 14px;
    height: 24px;
    border: none;
    /* input에 focus가 될 때 border를 없애려면 */
    outline: none;
    width: 100%;
    background-color: transparent;
}
.ks-formbox input[type="radio"] {
    line-height: 24px;
}
.ks-formbox input[type=radio] + label {
    font-size: 14px;
    line-height: 24px;
    padding: 0 7px;
}
.ks-formbox input[type="date"] {
    font-size: 14px;
    line-height: 24px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}
.ks-formbox input[type="time"] {
    font-size: 14px;
    line-height: 24px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}
.ks-formbox textarea {
    font-size: 14px;
    line-height: 24px;
    width: 100%;
    height: 96px;
    border: none;
    outline: none;
    background-color: transparent;
}
/* ks-formbox 하단에 붙는 캡션용 */
.ks-formbox .kl-caption {
    font-size: 12px;
    padding: 0 12px;
    padding-top: 12px;
    color: #656565;
}
.ks-formbox .kl-caption ul {
    list-style-type: disc;
}

.ks-formbox select {
    font-size: 14px;
    line-height: 24px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}

/* ------------------------- My Style -------------------------------- */

.ks-absolute-center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

/* 작업 목록 테이블 */
.task-list-table tbody tr { @apply table w-full box-border; }
.task-list-table thead th { @apply text-center; }
.task-list-table td { @apply border-t-0 text-center; }

.task-list-table tr th:nth-of-type(1),
.task-list-table tr td:nth-of-type(1) { @apply w-[50px]; }
.task-list-table tr th:nth-of-type(2),
.task-list-table tr td:nth-of-type(2) { @apply w-[250px]; }
.task-list-table tr th:nth-of-type(3) { width: calc(100% - 300px); }
.task-list-table tr td:nth-of-type(3) { @apply border-r-0; }

/* 작업 목록 공통 */
.kl-task-list { @apply flex flex-col justify-start items-start gap-0 list-none p-0; }
.kl-task-list li { width: 100%; }

/* 작업 체크 박스 */
.kl-task-checker input[type=checkbox] + label { border: 1.5px solid #aaa; }
.kl-task-checker input[type=checkbox]:checked + label { border: 1.5px dotted #fff; transition: all 0.7s ease; }


